"use client"

import { EmptyState } from "@/components/EmptyState"
import { JobCard } from "@/components/JobCard"
import { SingleJobSkeleton } from "@/components/JobSkeleton"
import { useUser } from "@/contexts/UserContext"
import { api } from "@/lib/api"
import { useCallback, useEffect, useState } from "react"
import { toast } from "sonner"

interface FavoriteJob {
  jobId: string
  jobData: {
    id: number
    title: string
    company_name: string
    company_logo: string | null
    category: string
    candidate_required_location: string
    description: string
  }
}

type PageState = {
  status: "loading" | "no-user" | "no-favorites" | "has-favorites"
  favorites: FavoriteJob[]
}

const LoadingState = () => (
  <div className="space-y-8 animate-in fade-in duration-300">
    <div className="h-8 w-48 bg-zinc-200 dark:bg-zinc-800 rounded animate-pulse" />
    <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-3">
      {Array.from({ length: 6 }).map((_, i) => (
        <SingleJobSkeleton key={i} />
      ))}
    </div>
  </div>
)

const FavoritesList = ({
  favorites,
  onUnfavorite,
}: {
  favorites: FavoriteJob[]
  onUnfavorite: (jobId: string) => Promise<void>
}) => (
  <div className="space-y-8 animate-in fade-in duration-300">
    <h1 className="text-2xl font-semibold text-green-400">Vagas favoritadas</h1>
    <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-3">
      {favorites.map((favorite) => (
        <JobCard
          key={favorite.jobId}
          id={favorite.jobData.id}
          title={favorite.jobData.title}
          companyName={favorite.jobData.company_name}
          companyLogo={favorite.jobData.company_logo || undefined}
          category={favorite.jobData.category}
          location={favorite.jobData.candidate_required_location}
          description={favorite.jobData.description}
          isFavorited={true}
          onFavoriteClick={() => onUnfavorite(favorite.jobId)}
        />
      ))}
    </div>
  </div>
)

export default function FavoritedJobs() {
  const { userId } = useUser()
  const [pageState, setPageState] = useState<PageState>({
    status: "loading",
    favorites: [],
  })

  const loadFavorites = useCallback(async () => {
    try {
      if (!userId) {
        await new Promise((resolve) => setTimeout(resolve, 500))
        setPageState({ status: "no-user", favorites: [] })
        return
      }

      const response = await api.get<FavoriteJob[]>("/favorited-jobs", {
        headers: { "X-User-Id": userId },
      })

      await new Promise((resolve) => setTimeout(resolve, 500))

      setPageState({
        status: response.data.length > 0 ? "has-favorites" : "no-favorites",
        favorites: response.data,
      })
    } catch (error) {
      console.error("Erro ao carregar favoritos:", error)
      toast.error("Falha ao carregar favoritos")
      setPageState({ status: "no-favorites", favorites: [] })
    }
  }, [userId])

  const handleUnfavorite = async (jobId: string) => {
    try {
      const job = pageState.favorites.find((fav) => fav.jobId === jobId)
      if (!job) return

      await api.post(
        "/favorited-jobs/toggle",
        { jobId },
        {
          headers: { "X-User-Id": userId },
        }
      )

      const newFavorites = pageState.favorites.filter(
        (fav) => fav.jobId !== jobId
      )
      setPageState({
        status: newFavorites.length > 0 ? "has-favorites" : "no-favorites",
        favorites: newFavorites,
      })

      toast.success("Vaga removida dos favoritos")
    } catch (error) {
      console.error("Erro ao remover vaga dos favoritos:", error)
      toast.error("Falha ao remover vaga dos favoritos")
    }
  }

  useEffect(() => {
    loadFavorites()
  }, [loadFavorites])

  switch (pageState.status) {
    case "loading":
      return <LoadingState />

    case "no-user":
      return (
        <EmptyState
          title="ID do usuário não encontrado"
          message="Por favor, atualize a página para gerar um ID de usuário."
        />
      )

    case "no-favorites":
      return (
        <EmptyState
          title="Nenhuma vaga favoritada"
          message="As vagas que você favoritar aparecerão aqui. Vá explorar algumas vagas!"
        />
      )

    case "has-favorites":
      return (
        <FavoritesList
          favorites={pageState.favorites}
          onUnfavorite={handleUnfavorite}
        />
      )
  }
}
